export default {
	 name:'Textarea',
	 props: {
		  placeholderStyle:{
			type: String,
			default:'color:#F76260'
		  },
		  maxlength:{
			 type:Number,
			 default:-1 
		  }
		},
		data() {
			return {
			   visible:false,
			   message:'',
			   position:'placemiddle',
			   duration:2000,
			   icon:''
			}
		},
		onLoad(e) {
			
		},
		created() {
	    
	    },
		methods: {
	        blurFun(e){ //输入框失去焦点时触发
				
			},
			focusFun(e){ //输入框聚焦时触发
				
			},
			inputFun(e){ //当键盘输入时
				
			},
		},
		 computed: {
			
        }
	}
